import React from 'react'
import styled from 'styled-components'
import {Grid, Row, Col} from 'react-flexbox-grid'
import Dialog from 'material-ui/Dialog'
import FlatButton from 'material-ui/FlatButton'

// import {IconLink, PrimaryNavigation, Block} from 'components'

const Wrapper = styled.nav `
background: red;
`

class Header extends React.Component {

  state = {
    open: false
  };

  handleOpen = () => {
    this.setState({open: true});
  };

  handleClose = () => {
    this.setState({open: false});
  };

  render() {

    const actions = [ < FlatButton label = "Cancel" primary = {
        true
      }
      onTouchTap = {
        this.handleClose
      } />, < FlatButton label = "Submit" primary = {
        true
      }
      keyboardFocused = {
        true
      }
      onTouchTap = {
        this.handleClose
      } />
    ];

    return (
      <Wrapper>
        <Grid>
          <Row>
            <Col xs={3} sm={3} md={3} lg={3}>logo</Col>
            <Col xs={6} sm={6} md={6} lg={6}>caja <a onTouchTap={this.handleOpen}>ad</a></Col>
            <Col xs={3} sm={3} md={3} lg={3}>Menu</Col>
          </Row>
        </Grid>
        <Dialog title="Dialog With Actions" actions={actions} modal={false} open={this.state.open} onRequestClose={this.handleClose}>
          The actions in this window were passed in as an array of React objects.
        </Dialog>
      </Wrapper>
    )
  }
}

export default Header
